@import '../../config.css';
.hero{
    width: 100%;
    min-height: 100vh;
    background: linear-gradient(var(--un),30%,var(--zero),60%,var(--cinq )),
        url('../../assets/placeholder/hero.png');
    background-size: cover;
    background-position: center;
    /* Color of text in component  */
    color: var(--cinq);
    display: flex;
    align-items: center;
    justify-content: center;
}
.hero_text{
    text-align: center;
    max-width: 800px;
}
.hero_text h1{
    font-size: 60px;
    font-weight: 600;
}
.hero_text p{
    max-width: 700px;
    margin: 10px auto 20px;
    line-height: 1.4;
}
/* Media Query */
@media (max-width: 850px){
    .hero_text h1{
        font-size: 40px;
    }
    
}
@media (max-width: 650px){
    .hero_text h1{
        font-size: 30px;
        max-width: 400px;
        margin: auto;
    }
    .hero_text p{
        font-size: 14px;
        margin: 15px auto 30px;
    }
}