@import '../../config.css';
nav{
    width: 100%;
    /* background: #333; */
    color: var(--cinq);
    padding: 4px 0;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 1000;
}
.logo{
    width: 180px;
}
nav ul li{
    display: inline-block;
    list-style: none;
    margin: 5px 20px;
    font-size: 16px;
}
.dark_nav{
    background: var(--un);
    transition: all 0.5 ease;
}
.menu_icon{
    display: none;

}

/*media query*/
@media (max-width: 1000px){
    .logo{
        width: 140px;
    }
    nav ul li{
        margin: 10px 15px;
    }
}
@media (max-width: 840px){
    nav{
        padding: 15px 0;
    }
    nav ul {
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0px;
        background: var(--un);
        z-index: -1;
        width: 200px;
        padding-top: 70px;
        transition: all 0.5s ease;


    }
    nav ul li{
        display: block;
        margin: 25px 40px;

    }
    .menu_icon{
        display: block;
        width: 30px;
        cursor: pointer;
    }
    .hide_mobile_menu{
        right: -200px;
        transition: all 0.5s ease;
    }
}