@import '../../config.css';
.programs{
    margin: 80px auto;
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.program img{
    width: 100%;
    border-radius: 10px;
    display: block;
}
.program{
    flex-basis: 31%;
    position: relative;
}
.caption{
    border-radius: 10px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(var(--zero),10%,var(--un));
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: var(--cinq);
    cursor: pointer;
    /* Hide image and caption */
    opacity: 0;
    padding-top: 70%;
    transition: all 0.4s ease;
}
.caption img{
    width: 60px;
    margin-bottom: 10px;
}
/* Opacity change on hover */
.program:hover .caption{
    opacity: 1;
    padding-top: 0%;
}
/* Media Query */
@media (max-width: 650px){
    .programs{
        flex-direction: column;
    }
    .program{
        flex-basis: 100%;
        margin: 20px;
    }
}